*{
    margin: 0;
    padding: 0;
}

.logo-container {
    width: 130px;
    height: 130px;
    background-image: url('./Images/logo.jpg');
    background-size: contain; 
    background-repeat: no-repeat; 
    background-position: center; 

}

nav img{
    width:50%;
} 

nav{
    background-color:rgb(90, 222, 96);
    color:black;
    display: flex;
    padding:10px;
    justify-content: space-between;
    align-items: center;
}
 
nav ul{
    display:flex;
    justify-content:space-between;
    align-items: center;
    width:50%;
    margin-left:100px;
    
    
}
nav ul a{
    font-size:20px;
    font-weight:700;
    color:black;
    text-decoration: none;
}
nav ul a:hover
{
    color:crimson;
}