.footer {
    background-color: #333;
    color: #fff;
    padding: 40px 0;
    text-align: center;
    margin-top: 60px;
}

.footer-container {
    display: flex;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;
}

.footer-section {
    width: 30%;
}

.footer-section h3 {
    margin-bottom: 15px;
    font-size: 18px;
    color: #f1f1f1;
}

.footer-section p, .footer-section ul {
    font-size: 14px;
    color: #bbb;
}

.footer-section ul {
    list-style-type: none;
}

.footer-section ul li {
    margin: 10px 0;
}

.footer-section ul li a {
    text-decoration: none;
    color: #bbb;
    transition: color 0.3s;
}

.footer-section ul li a:hover {
    color: #fff;
}

.footer-bottom {
    margin-top: 30px;
    background-color: #222;
    padding: 10px;
    font-size: 12px;
}
