*{
    margin: 0;
    padding: 0;
}
.Form h2{
    background-color:white;
    padding: 2rem;
    width: 100%;
    font-size: 50px;
    justify-content: center;
    text-align: center;
}
.Container{
    display: flex;
    justify-content:space-between;
    justify-content: space-around;
    align-items:center;  
}
.Form button{
    font-size: 20px;
    margin-left: 35%;
    margin-top: 50%;
    background: rgb(79, 217, 79);
    border-radius: 5px;
}
.Form button:hover{
    cursor: pointer;
}
.Form label{
    font-size: 20px;
    margin-top: 10px;
}
.Form input{
    margin-left: 5px;

}
